<template>
  <div class="about">
    <h1 @click="$router.go(-1)">返回</h1>
    <div>
      {{sszz.title}}
    </div>
  </div>
</template>
<script> 
export default {
  name:'About',
  data(){
    return {
      sszz:''
    }
  },
  mounted(){
    axios.get("https://cnodejs.org/api/v1/topic/"+this.$route.params.id).then((res) => {
        if ((res.status >= 200 && res.status < 300) || res.status == 304) {
          this.sszz = res.data.data;   
        }
      });
  }
}
</script>

<style scoped>
.about{
  font-size: .14rem;
}
</style>
